
import {
  defineComponent,
  ref,
  computed,
  onMounted,
} from "vue";
import { useRoute, useRouter } from 'vue-router'

import * as Connection from "../../src/connection";

import ServerInOutPortsSelect from "../a/serverIn-out-ports-select.vue";
import Page from "../a/page-component.vue";
import Card from "../a/card-component.vue";
import CardBody from "../a/card-body.vue";
import RowCell from "../a/row-cell.vue";
import Btn from "../a/btn-component.vue";

export default defineComponent({
  components: {
    ServerInOutPortsSelect,
    Page,
    Card,
    CardBody,
    RowCell,
    Btn,
  },
  setup() {
    const route = useRoute()
    const router = useRouter()    
    const inputToAdd = ref(-1);

    const filterid = computed(() => {
      return route.params.filterid;
    });

    const midiinid = computed(() => {
      return route.params.midiinid;
    });

    const chainid = computed(() => {
      return route.params.chainid;
    });

    const headrMsg = computed(() => {
      return `${midiinid.value}-${chainid.value} Add Midi Destination`;
    });

    const midiRouteInput = computed(() => {
      return Connection.loginStatus.userDataConfig.getMidiRouteInput(
        Connection.loginStatus.inPorts[Number(midiinid.value)]
      );
    });

    const filterMidiDestination = computed(() => {
      return midiRouteInput.value.midiRouterChains[
        parseInt(chainid.value as string)
      ].getFilterMidiDestination(parseInt(filterid.value as string));
    });

    const inPortsWithoutSelf = computed(() => {
      return midiRouteInput.value.inPortsWithoutSelf;
    });

    function doOk() {
      if (inputToAdd.value === -1) {
        return;
      }
      const inputObj = inPortsWithoutSelf.value.filter((row) => {
        return row.midiInputId === inputToAdd.value;
      })[0];
      if (filterid.value === "-1") {
        midiRouteInput.value.midiRouterChains[
          parseInt(chainid.value as string)
        ].addFilterMidiDestination(inputObj);
      } else {
        filterMidiDestination.value.setVal(inputObj);
      }
      router.push(`/midiin/${midiinid.value}`);
    }

    function doCancel() {
      if (filterid.value === "-1") {
        router.push(`/AddChainFilter/${midiinid.value}/${chainid.value}`);
      } else {
        router.push(`/midiin/${midiinid.value}`);
      }
    }

    onMounted(() => {
      if (filterid.value === "-1") {
        return;
      }
      inputToAdd.value =
        filterMidiDestination.value.baseMidiRouteInput.midiInputId;
    });

    return {
      doCancel,
      doOk,
      inPortsWithoutSelf,
      filterMidiDestination,
      midiRouteInput,
      headrMsg,
      chainid,
      midiinid,
      filterid,
      inputToAdd,
    };
  },
});
